@media only screen and (max-width: 800px) and (max-height: 1000px) /* Phones */{
    .navbar {
      height: 6vh;
      background: linear-gradient(30deg, #FAF9F6 45.3%,  #209B47 90.6%); /* Set the background color the linear color */

    }
    .logo-container {
      display: flex;
      align-items: center;
      margin: 0;
      padding-top: 5px;
      padding-left: 0px;
    }
    
    .logo {
      height: 40px;
      width: auto;
      margin:0;
    }
    
    .logo-text {
      margin: 0;
      font-size: 1.5rem;
    }
    .desktop-menu {
      display: none; /* Hide desktop menu by default on mobile */
    }   
    

  }